import axios from "axios";

class Service {
  static getOptions() {
    return new Promise((resolve, reject) => {
      const query = `
      query countryQuery {
        countries {
          country
        }
      }`;
      axios
        .post("/graphql", { query: query })
        .then(result => {
          const { data } = result.data;
          resolve(data);
        })
        .catch(error => {
          reject(error);
        });
    });
  }

  static getData(countries = []) {
    return new Promise((resolve, reject) => {
      const query = `
      query statsQuery {
          stats(countries: ${JSON.stringify(countries)}) {
            country
            cases
            deaths
            active
            critical
            recovered
            todayCases
            todayDeaths
            casesPerOneMillion
          }
        }
      `;
      axios
        .post("/graphql", { query: query })
        .then(result => {
          const { data } = result.data;
          resolve(data);
        })
        .catch(error => {
          reject(error);
        });
    });
  }
}

export default Service;
